.news {
	&_wrap {
		height: 254px;
		overflow: hidden;
	}

	&_box {
		.hd ul {
			width: 700px;
			@extend .clearfix;

			li {
				position: relative;
				top: -10px;
				width: 140px;
				height: 40px;
				line-height: 40px;
				float: left;
				font-size: 18px;
				letter-spacing: 2px;
				text-align: center;
				cursor: pointer;
				a {
					display: block;
				}

				i {
					margin-right: 6px;
				}

				&:hover,
				&.on {
					color: $deputy-color;

					&:after {
						content: '';
						position: absolute;
						bottom: 0;
						left: 50%;
						transform: translateX(-50%);
						@include size(80px, 2px);
						background-color: $deputy-color;
					}
				}
			}
		}

		.bd ul {
			float: left;
			width: 700px;
			height: 284px;
			background-color: $bg-color;
			@extend .clearfix;

			li {
				padding: 15px;
			}
		}
	}


	&_item {
		position: relative;
		display: block;
		font-size: 14px;
		height: 36px;
		line-height: 36px;
		padding-left: 20px;
		border-bottom: 1px dashed #b4b4b4;
		@extend .clearfix;
		&.ful {
			p {
				width: 100%;
			}
		}
		&:before {
			content: '';
			position: absolute;
			left: 8px;
			top: 15px;
			@include size(4px, 4px);
			display: block;
			background-color: $deputy-color;
			transform: rotate(45deg);
		}

		p {
			float: left;
			width: 75%;
			@extend .text_ellipsis;

			&:hover {
				color: $deputy-color;
			}
		}

		span {
			float: right;
			color: $nine-color;
		}
	}
}