.affairs {
	&_menu {
		@extend .clearfix;
		padding: 18px 5px;
		height: 100%;
		border-top: 8px solid $border-color;
		background-color: $white-color;
	}

	&_item {
		float: left;
		position: relative;
		width: 170px;
		padding-left: 16px;
		&:after {
			content: '';
			position: absolute;
			right: 0;
			top: 50%;
			transform: translateY(-50%);
			@include size(1px,30px);
			background-color: $border-color;
		}
		&:last-child() {
			&:after {
				display: none;
			}
		}

		a {
			display: block;
			@extend .clearfix;

			i {
				float: left;
				font-size: 30px;
				margin-top: 3px;
				margin-right: 12px;
				color: $main-color;
			}

			span {
				float: left;
				width: 90px;
				font-size: 14px;
				color: $six-color;

				&:hover {
					color: $deputy-color;
				}
			}
		}
	}

	&_left {
		width: 330px;
		height: 326px;
		margin-right: 20px;

		&_item {
			float: left;
			display: block;
			margin-right: 7px;
			margin-bottom: 7px;
			padding: 0 4px;
			@include size(105px, 105px);
			text-align: center;
			background-color: $bg-color;
			transition: all .3s;

			&:hover {
				box-shadow: 0 1px 10px lighten($main-color, 35%);
				p {
					color: $deputy-color;
				}
			}

			p {
				@extend .text_ellipsis;
				font-size: 14px;
			}

			&:nth-child(3n+3) {
				margin-right: 0;
			}
		}

		&_icon {
			margin: 10px auto;
			@include size(48px, 48px);
			text-align: center;
			background-color: lighten($main-color, 20%);
			color: $white-color;
			border-radius: 50%;

			i {
				font-size: 26px;
				line-height: 48px;
			}
		}
	}

	&_mid {
		width: 486px;
		height: 326px;
		overflow: hidden;
		.hd ul {
			@extend .clearfix;
			margin-bottom: 24px;

			li {
				position: relative;
				float: left;
				margin-right: 15px;
				@include size(152px, 50px);
				line-height: 50px;
				text-align: center;
				background-color: lighten($main-color, 42%);
				color: $main-color;
				cursor: pointer;

				&:last-child {
					margin-right: 0;
				}

				&.on {
					background-color: $main-color;
					color: $white-color;
					&:after {
						content: '';
						display: block;
						position: absolute;
						bottom: -8px;
						left: 50%;
						width: 0;
						height: 0;
						border-width: 8px 8px 0;
						border-style: solid;
						border-color: $main-color transparent transparent;
						transform: translateX(-50%);
					}
				}
			}
		}

		&_title {
			@include size(100%, 50px);
			line-height: 50px;
			padding: 0 20px;
			font-size: 18px;
			font-weight: bold;
			color: #be0303;
			background-color: #fff7ea;
			&:hover {
				color: lighten(#be0303,10%);
			}

			a {
				display: block;
				@extend .text_ellipsis;
			}
		}

		&_box {
			padding-top: 20px;
			height: 200px;
			overflow: hidden;
		}
	}
	&_right {
		width: 284px;
		height: 326px;
		overflow: hidden;
		&_top {
			@extend .clearfix;
			li {
				float: left;
				overflow: hidden;
				margin-right: 10px;
				margin-bottom: 10px;
				text-align: center;
				@include size(88px,76px);
				border: 1px solid $border-color;
				border-radius: 3px;
				&:nth-child(3n+3) {
					margin-right: 0;
				}
				&:hover {
					border-color: lighten($main-color,26%);
					p {
						color: $deputy-color;
					}
				}
				i {
					display: block;
					font-size: 32px;
					margin-top: 8px;
					margin-bottom: 8px;
				}
				p {
					@extend .text_ellipsis;
					font-size: 14px;
					color: $six-color;
				}
				&:nth-child(1){
					i {
						color: #fdac39;
					}
				}
				&:nth-child(2){
					i {
						color: #d43939;
					}
				}
				&:nth-child(3){
					i {
						color: #ff6a4a;
					}
				}
				&:nth-child(4){
					i {
						color: #3aad73;
					}
				}
				&:nth-child(5){
					i {
						color: #0b93c7;
					}
				}
				&:nth-child(6){
					i {
						color: #95ca40;
					}
				}
			}
		}
		&_tips {
			@extend .clearfix;
			padding-top: 12px;
			li {
				float: left;
				margin-right: 8px;
				margin-bottom: 10px;
				padding: 6px 16px;
				font-size: 13px;
				color: $six-color;
				background-color: $bg-color;
				border: 1px solid $ccc-color;
				border-radius: 30px;
				&:hover {
					color: $deputy-color;
				}
			}
		}
	}
}