.article {
	&_title {
		@include size(100%,84px);
		@extend .text_ellipsis;
		font-size: 24px;
		line-height: 82px;
		font-weight: bold;
		text-align: center;
		color: $main-color;
		border-top: 1px solid $border-color;
		border-bottom: 1px solid $border-color;
		@include bg('../img/news_detail_bg.jpg',cover);
		cursor: pointer;
	}
	&_tips {
		@include lineht(42px);
		text-align: center;
		font-size: 16px;
		border-bottom: 1px solid $border-color;
		color: $six-color;
		span {
			margin: 0 20px;
		}
		i {
			display: inline-block;
			margin: 0 4px;
			cursor: pointer;
		}
	}
	&_color {
		i {
			@include size(16px,16px);
			border: 1px solid #3c3e10;
		}
	}
	&_details {
		min-height: 500px;
		padding: 20px;
		p {
			margin-bottom: 10px;
			text-indent: 2em;
			text-align: justify;
			line-height: 1.5;
		}
		&.large {
			p {
				font-size: 18px!important;
			}
		}
		&.middle {
			p {
				font-size: 16px!important;
			}
		}
		&.small {
			p {
				font-size: 14px!important;
			}
		}
	}
	&_pages {
		@extend .text_ellipsis;
		text-align: center;
		@include lineht(46px);
		border-top: 1px solid $border-color;
		border-bottom: 1px solid $border-color;
		span {
			margin-left: 10px;
			margin-right: 10px;
			font-weight: bold;
		}
		a {
			&:hover {
				color: $main-color;
			}
		}
	}
	&_function {
		padding: 46px 0;
		&_left {
			line-height: 36px;
		}
	}
}