.leader {
	&_box {
		border-top: 1px solid $border-color;
	}
	&_wrap {
		min-height: 500px;
		&_title {
			@extend .clearfix;
			li {
				float: left;
				margin-right: 24px;
				margin-bottom: 24px;
				text-align: center;
				font-size: 18px;
				@include size(140px,50px);
				line-height: 50px;
				border-radius: 5px;
				background-color: $border-color;
				cursor: pointer;
				&.active {
					@include colbg($white-color,$main-color);
				}
			}
		}
		&_cont {
			display: none;
			opacity: 0;
			&.active {
				display: block;
				opacity: 1;
			}
		}
	}
	&_list {

	}
	&_item {
		@extend .clearfix;
		margin-bottom: 24px;
		line-height: 42px;
		h6 {
			float: left;
			@include size(140px,42px);
			text-align: center;
			color: $main-color;
			background-color: lighten($main-color,40%);
		}
		ul {
			float: right;
			width: 994px;
			padding-left: 16px;
			background-color: $bg-color;
			li {
				float: left;
				width: 160px;
				a {
					display: block;
				}
				i {
					margin-right: 6px;
					font-size: 20px;
					color: $main-color;
				}
			}
		}
	}
}