.index {
	&_tab {
		position: relative;
		margin-top: 12px;
		z-index: 66;

		&_list {
			display: none;
		}

		&_item {
			display: none;
			opacity: 0;

			&.on {
				display: block;
				opacity: 1;
			}
		}

		&_btm {
			height: 90px;
		}
	}

	&_wrap {
		position: relative;
	}

	&_nav {
		@extend .clearfix;
		background-color: rgba(46, 143, 211, .7);
		opacity: 1;

		&.transparent {
			background-color: $main-color;
		}

		&.index {
			.index_nav_item {
				width: 228px;
			}
		}

		&_item {
			position: relative;
			float: left;
			width: 240px;
			height: 50px;
			text-align: center;
			line-height: 50px;
			font-size: 17px;
			font-weight: bold;
			color: #fff;

			a {
				display: block;
			}

			&.on,
			&:hover {
				background-color: darken($main-color, 5%);
			}
		}

		&_item:after {
			content: '';
			display: block;
			position: absolute;
			right: 0;
			top: 50%;
			transform: translateY(-50%);
			@include size(1px, 20px);
			background-color: $white-color;
			opacity: .3;
		}

		&_item:nth-last-child(1):after {
			display: none;
		}
	}

	&_action {
		position: absolute;
		right: 0;
		top: 0;
		text-align: center;
		line-height: 50px;
		@include size(60px, 50px);
		color: $white-color;
		background-color: #14a6bf;
		cursor: pointer;

		&:hover {
			background-color: lighten(#14a6bf, 5%);
		}
	}

	&_box {
		position: relative;
		padding: 20px 30px;
		height: 364px;
		background-color: $white-color;
		&_news {
			width: 714px;
		}
	}
}