.topic {
	&_title {
		@include size(120px, 90px);
		line-height: 35px;
		padding: 10px 20px;
		font-size: 28px;
		text-align: center;
		color: $white-color;
		background-color: $deputy-color;
		cursor: pointer;
	}

	&_right {
		.bd ul {
			@extend .clearfix;

			li {
				float: left;
				@include size(265px, 90px);
				margin-right: 4px;
			}
		}
	}
	&_wrap {
		min-height: 540px;
		position: relative;
		padding-bottom: 80px;
		border-top: 1px solid $border-color;
	}
	&_list {
		padding-top: 30px;
		@extend .clearfix;
		li {
			float: left;
			@include size(265px,90px);
			margin-right: 20px;
			margin-bottom: 20px;
			transition: all .3s ease-in-out;
			a {
				display: block;
			}
			&:hover {
				box-shadow: 0 3px 16px rgba($color: $main-color, $alpha: .4);
			}
		}
	}
	&_page {
		position: absolute;
		left: 0;
		bottom: 20px;
		width: 100%;
	}
}