$appeal_border_col: #d3e6f4;

.appeal {
	&_box {
		padding-top: 30px;
		min-height: 540px;
		border-top: 1px solid $border-color;
	}

	&_title {
		@include lineht(48px);
		@extend .clearfix;
		background-color: #d3e6f4;

		.appeal_form_txt select {
			border-color: $white-color;
		}

		span {
			float: left;
			display: block;

			i {
				margin-right: 8px;
				cursor: pointer;
			}
		}

		&_left {
			width: 146px;
			text-align: center;
			font-size: 18px;
			font-weight: bold;
			color: $main-color;
		}

		ul {
			@extend .clearfix;

			li {
				position: relative;
				float: left;
				width: 180px;
				font-size: 18px;
				font-weight: bold;
				color: $main-color;
				text-align: center;
				cursor: pointer;

				&.active {
					&:after {
						content: '';
						position: absolute;
						top: 0;
						left: 0;
						@include size(100%, 3px);
						background-color: darken($main-color, 20%);
					}

					background-color: $white-color;
				}
			}
		}
	}

	&_wrap {
		display: none;
		opacity: 0;
		width: 100%;

		&.active {
			display: block;
			opacity: 1;
		}
	}

	&_pages {
		padding: 46px 0;
	}

	&_tb {
		table {
			width: 100%;
			text-align: center;
			line-height: 56px;
			border-color: $border-color;

			tbody {
				tr:nth-child(odd) {
					background-color: $bg-color;
				}

				td {
					&.already {
						color: #079002;
					}

					&.doing {
						color: $deputy-color;
					}
				}
			}
		}
	}

	&_cont {
		max-width: 300px;
		@extend .text_ellipsis;
		padding: 0 20px;
	}

	&_form {
		margin-bottom: 30px;

		&_list {
			line-height: 48px;

			li {
				@extend .clearfix;
				border-bottom: 1px solid $appeal_border_col;
				border-left: 1px solid $appeal_border_col;
				border-right: 1px solid $appeal_border_col;
			}
		}

		&_title {
			position: relative;
			float: left;
			width: 146px;
			text-align: center;
			background-color: #f2f7fc;
			border-right: 1px solid $appeal_border_col;
			&.area {
				height: 280px;
			}

			&:before {
				content: '*';
				display: inline-block;
				margin-right: 4px;
				color: lighten($deputy-color, 20%);
			}
		}

		&_txt {
			float: left;
			padding-left: 20px;

			span {
				font-size: 15px;
				color: $nine-color;
			}

			img {
				display: inline-block;
				margin-top: 12px;
				margin-left: 10px;
			}

			input,
			select {
				@include size(300px, 34px);
				line-height: 34px;
				text-indent: 10px;
				font-size: 15px;
				@include placehold($nine-color,15px);
				border: 1px solid $border-color;
				background-color: transparent;
			}

			textarea {
				@include size(950px,240px);
				margin-top: 18px;
				padding-top: 8px;
				text-indent: 10px;
				border: 1px solid $border-color;
				@include placehold($nine-color,15px);
				resize: none;
			}

			input[type=checkbox] {
				@include size(inherit, inherit);
				line-height: inherit;
			}
		}
		&_btn {
			height: 126px;
			padding: 45px 0;
			text-align: center;
			border: 1px solid $appeal_border_col;
			border-top: none;
			button {
				display: inline-block;
				margin: 0 20px;
				@include size(112px,34px);
				line-height: 34px;
				text-align: center;
				border-radius: 4px;
				color: $white-color;
			}
			.submit {
				background-color: #4c9c30;
				&:hover {
					background-color: lighten(#4c9c30,10%);
				}
			}
			.reset {
				background-color: #e89c30;
				&:hover {
					background-color: lighten(#e89c30,10%);
				}
			}
		}
	}
}