.pages {
	&_box {
		@extend .clearfix;
		a {
			display: block;
		}
		&>a,
		ul li {
			float: left;
			margin: 0 6px;
			@include size(32px, 32px);
			line-height: 32px;
			text-align: center;
			font-size: 13px;
			border: 1px solid $border-color;
			border-radius: 5px;
			color: $nine-color;
			&:hover {
				border-color: $main-color;
				color: $main-color;
			}
		}

		ul li {
			&.active {
				border-color: $main-color;
				color: $main-color;
			}
		}
	}
}