.footer {
	&_box {
		width: 100%;
		background-color: $bg-color;
	}

	&_top {
		position: relative;
		z-index: 77;
		padding-top: 14px;
	}

	&_cell {
		position: relative;
		width: 186px;
		float: left;
		margin-right: 16px;
		cursor: pointer;

		i {
			position: absolute;
			right: 10px;
			top: 8px;
		}

		&:hover {
			.footer_unit {
				display: block;
			}
			i {
				color: $main-color;
			}
		}

		&:last-child {
			margin-right: 0;
		}
	}

	&_txt {
		@extend .text_ellipsis;
		width: 186px;
		height: 32px;
		line-height: 32px;
		padding-left: 8px;
		border-radius: 3px;
		background-color: $white-color;
	}

	&_unit {
		display: none;
		padding: 0 8px;
		position: absolute;
		bottom: 32px;
		left: 0;
		width: 100%;
		max-height: 300px;
		overflow-y: scroll;
		background-color: #fff;
		border-radius: 3px;

		&::-webkit-scrollbar {
			display: inherit;
			width: 4px;
			height: 4px;
		}

		&::-webkit-scrollbar-thumb {
			border-radius: 5px;
			box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
			background: rgba(0, 0, 0, 0.2);
		}

		&::-webkit-scrollbar-track {
			box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
			border-radius: 0;
			background: rgba(0, 0, 0, 0.1);
		}

		li {
			line-height: 30px;
			border-bottom: 1px dashed #b4b4b4;

			&:hover {
				color: $deputy-color;
			}
		}
	}

	&_btm {
		padding: 12px 0;
	}

	&_left {
		margin-left: 120px;
		margin-top: 5px;
	}

	&_mid {
		width: 800px;
		font-size: 13px;
		line-height: 26px;
		text-align: center;

		a {
			margin: 0 5px;

			&:hover {
				color: $deputy-color;
			}
		}
	}

	&_right {
		margin-top: 8px;
	}
}