.header {
	&_container {
		font-size: 16px;
	}

	&_top {
		position: relative;
		margin-top: 18px;
		height: 90px;
	}

	&_logo {
		font-size: 0;
		position: absolute;
		// left: 370px;
		left: 0;
		// top: 50px;
		top: 0;
	}

	&_other {
		position: absolute;
		// left: 340px;
		left: 708px;
		top: 250px;
		// top: 200px;
	}

	&_news {
		position: relative;
		width: 100%;
		height: 90px;
		padding: 0 50px;
		text-align: center;
		font-family: '宋体';
		color: $main-color;
		background-color: $bg-color;
		border: 1px solid $ccc-color;

		&_wrap {
			width: 1098px;
			line-height: 30px;
			overflow: hidden;
			.bd ul {
				overflow: hidden;
			}
		}

		&_title {
			@extend .text_ellipsis;
			font-size: 32px;
			font-weight: bold;
			margin-top: 6px;
			letter-spacing: 2px;
		}

		&_cont {
			float: left;
			overflow: hidden;
			font-size: 18px;
			width: auto;
			height: 30px;
			line-height: 30px;
			margin-top: 6px;

			a {
				width: 100%;
				height: 30px;
				line-height: 30px;
				display: block;
				margin-right: 30px;
			}
		}
	}

	&_search {
		position: relative;
		overflow: hidden;
		margin-top: 10px;
		@include size(492px, 40px);
		line-height: 40px;
		border-radius: 5px;
		background-color: $white-color;

		input {
			@include size(100%, 100%);
			padding-left: 40px;
			@include placehold($nine-color);
		}

		i {
			display: block;
			@include position(10px, 10px);
			line-height: 1;
			font-size: 20px;
			color: $nine-color;
		}

		&_btn {
			display: block;
			position: absolute;
			right: 0;
			top: 0;
			@include size(110px, 40px);
			text-align: center;
			letter-spacing: 1px;
			@include colbg($white-color, $main-color);
		}
	}

	&_date {
		padding-top: 12px;
	}

	&_riqi {
		font-size: 16px;
		font-family: Arial;
		line-height: 25px;
		letter-spacing: 1px;
		color: $white-color;
	}
}