.walkin {
	&_tab {
		@include size(266px, 184px);

		.hd ul {
			bottom: 13px;
		}

		.bd ul li {
			@include size(266px, 184px);

			.banner_txt {
				@include lineht(40px);
			}
		}
	}

	&_top {
		margin-bottom: 20px;

		&_txt {
			@include size(350px, 184px);
			padding: 20px;
			background-color: $bg-color;

			h5 {
				font-size: 16px;
				font-weight: bold;
				margin-bottom: 6px;
				color: $main-color;
			}

			p {
				font-size: 14px;
				text-indent: 2em;
				line-height: 24px;
				text-align: justify;

				a {
					color: $main-color;
				}
			}
		}

		&_right {
			width: 500px;
			height: 184px;
			overflow: hidden;
		}

		&_title {
			h6 {
				position: relative;
				padding-left: 10px;
				font-size: 16px;
				font-weight: bold;
				color: $main-color;

				&:before {
					content: '';
					position: absolute;
					left: 0;
					top: 50%;
					transform: translateY(-50%);
					@include size(4px, 18px);
					background-color: $main-color;
				}
			}
		}
	}

	&_person {
		@extend .clearfix;
		padding-top: 12px;

		li {
			float: left;
			@include size(92px, 26px);
			line-height: 26px;
			margin-right: 10px;
			margin-bottom: 12px;
			text-align: center;
			@include colbg($white-color, #89b3ec);
			font-size: 14px;

			a {
				display: block;
			}

			&:nth-child(5n+5) {
				margin-right: 0;
			}
		}
	}

	&_lunbo {
		position: relative;
		overflow: hidden;

		.hd a {
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			@include size(20px, 60px);
			text-align: center;
			line-height: 60px;
			color: $white-color;
			background-color: rgba($color: #000000, $alpha: .4);
			z-index: 3;

			&.next {
				right: 0;
			}

			&.prev {
				left: 0;
			}
		}

		.bd ul {
			@extend .clearfix;

			li {
				position: relative;
				float: left;
				margin-right: 8px;
				@include size(136px, 120px);
				@extend .bg_cover;

				a {
					display: block;
					@include size(100%, 100%);
				}

				p {
					position: absolute;
					bottom: 0;
					left: 0;
					@extend .text_ellipsis;
					width: 100%;
					@include lineht(28px);
					text-align: center;
					font-size: 14px;
					@include colbg($white-color, rgba(0, 0, 0, .4));
				}
			}
		}
	}

	&_map {
		position: relative;
		width: 1200px;
		height: 90px;
		overflow: hidden;

		.bd ul {
			@extend .clearfix;

			li {
				float: left;
				@include size(126px, 90px);
				line-height: 90px;
				margin-right: 8px;
				text-align: center;
				font-size: 18px;
				font-weight: bold;
				background-color: $white-color;
				background-repeat: no-repeat;
				background-position: center;
				a {
					display: block;
				}
				i {
					margin-right: 4px;
					color: #89b3ec;
				}
			}
		}
	}
}