.service {
	&_tab {
		@include size(486px,226px);
		.bd ul li {
			@include size(486px,226px);
		}
	}
	&_left {
		width: 486px;
		margin-right: 20px;
	}
	&_left_btm {
		margin-top: 10px;
	}
	&_do {
		font-size: 16px;
		.hd ul {
			@extend .clearfix;
			li {
				float: left;
				width: 50%;
				@include lineht(36px);
				cursor: pointer;
				text-align: center;
				background-color: $border-color;
				i {
					margin-right: 6px;
				}
				&.on {
					@include colbg($white-color,$main-color);
				}
			}
		}
		.bd {
			@extend .clearfix;
			height: 52px;
			line-height: 52px;
			background-color: $bg-color;
			ul {
				float: left;
				width: 100%;
				li {
					position: relative;
					width: 25%;
					float: left;
					text-align: center;
					color: $six-color;
					&:hover {
						color: $deputy-color;
					}
					&:after {
						content: '';
						position: absolute;
						right: 0;
						top: 50%;
						transform: translateY(-50%);
						@include size(1px,16px);
						background-color: #dfdfdf;
					}
					&:last-child {
						&:after {
							display: none;
						}
					}
				}
			}
		}
	}
	&_mid {
		width: 366px;
		&_list {
			@extend .clearfix;
			li {
				float: left;
				width: 33.33%;
				height: 96px;
				text-align: center;
				border-right: 1px solid $white-color;
				border-top: 1px solid $white-color;
				background-color: $bg-color;
				&:nth-child(3n+3) {
					border-right: none;
				}
				a,i {
					display: block;
				}
				i {
					margin: 10px 0;
					font-size: 42px;
					color: $six-color;
				}
				p {
					@extend .text_ellipsis;
					font-size: 14px;
				}
				&:hover {
					@include colbg($white-color,$main-color);
					i {
						color: $white-color;
					}
				}
				&:last-child:hover {
					background-color: $bg-color;
				}
			}
		}
	}
	&_title {
		position: relative;
		@include lineht(36px);
		background-color: $bg-color;
		span {
			@include position(0,0);
			padding: 0 20px;
			background-color: $green-color;
			color: $white-color;
		}
	}
	&_right {
		@include size(252px,324px);
		overflow: hidden;
		background-color: $bg-color;
		&_list {
			padding: 0 10px;
		}
	}
	&_btm_list {
		@extend .clearfix;
		li {
			float: left;
			margin-right: 10px;
			@include size(232px,90px);
			box-shadow: 0 2px 10px rgba(0, 0, 0, .6);
			a {
				display: block;
			}
			&:nth-child(5n+5) {
				margin-right: 0;
			}
		}
	}
}