@import 'variable';
@import 'mixin';
@import 'reset';
@import 'mylei';

html,
body {
	@include size(100%, 100%);
	overflow: auto;
}

.webkit_box {
	display: -webkit-box;
	/* autoprefixer: off */
	-webkit-box-orient: vertical;
	/* autoprefixer: on */
	height: $ful-width;
}

.webkit_content {
	flex: 1;
	-webkit-box-flex: 1;
	overflow: auto;
	position: relative;

	&.hide {
		overflow: hidden;
	}
}

.wrap_container {
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	width: 100%;
	overflow: auto;
	padding-bottom: 24px;
	height: 100%;
	@extend .bg_cover;
}

.wrap_neiye {
	overflow: hidden;
	background-position: top center;
	background-repeat: no-repeat;
	background-size: 100% auto;
	background-color: #ebf8fe;
}

// 模态窗
.model {
	&_bg {
		position: fixed;
		left: 0;
		top: 0;
		@include size(100%,100%);
		background-color: rgba(0,0,0,.7);
		z-index: 666;
	}
	&_box {
		position: fixed;
		left: 50%;
		top: 50%;
		width: 480px;
		padding-top: 20px;
		font-size: 16px;
		transform: translate(-50%,-50%);
		background-color: $white-color;
		border-radius: 5px;
		z-index: 777;
	}
	&_title {
		padding: 30px;
	}
	&_close {
		position: absolute;
		right: -16px;
		top: -16px;
		@include size(30px,30px);
		padding: 3px;
		background-color: $white-color;
		border-radius: 50%;
		i {
			display: block;
			font-size: 25px;
			color: $ccc-color;
			transition: all .2s ease-in-out;
			&:hover {
				color: $main-color;
			}
		}
	}
	&_btn {
		padding: 20px 30px;
		text-align: right;
		a {
			display: inline-block;
			padding: 0 15px;
			margin: 0 6px;
			height: 28px;
			line-height: 26px;
			text-align: center;
			font-size: 12px;
			border-width: 1px;
			border-style: solid;
			border-radius: 2px;
			&.model_goon {
				background-color: $main-color;
				border-color: $main-color;
				color: $white-color;
			}
			&.model_giveup {
				background-color: $bg-color;
				border-color: $border-color;
				color: $six-color;
			}
		}
	}
}

.cheng_div {
	position: relative;
	width: 100%;
	height: 390px;
}

@import 'header';
@import 'index';
@import 'banner';
@import 'affair';
@import 'service';
@import 'news';
@import 'topic';
@import 'interact';
@import 'walkin';
@import 'huicui';
@import 'list';
@import 'appeal';
@import 'leader';
@import 'footer';
@import 'page';
@import 'article';

.common {
	&_more {
		display: block;

		&:hover {
			i {
				color: $main-color;
			}
		}

		i {
			font-size: 20px;
			color: $nine-color;
		}
	}
}

.bg_color {
	&_yellow {
		background-color: #f9f6af;
	}
	&_blue {
		background-color: #9ddbf4;
	}
	&_red {
		background-color: #f9d1d9;
	}
	&_gray {
		background-color: #dedede;
	}
	&_white {
		background-color: #fff;
	}
}