.huicui {
	&_left {
		width: 615px;
	}
	&_title {
		@extend .clearfix;
		border-bottom: 1px solid $ccc-color;
		li {
			float: left;
			@include size(205px,36px);
			line-height: 36px;
			border-right: 1px solid $ccc-color;
			background: $border-color;
			text-align: center;
			cursor: pointer;
			&:last-child {
				border-right: none;
			}
			i {
				font-size: 18px;
				margin-right: 8px;
			}
			&.active {
				@include colbg($white-color,$main-color);
			}
		}
	}
	&_body {
		@extend .clearfix;
		@include size(615px,288px);
		overflow: hidden;
		li {
			display: none;
			opacity: 0;
			float: left;
			padding: 15px;
			@include size(615px,288px);
			background-color: $bg-color;
			&.active {
				display: block;
				opacity: 1;
			}
			&>div,
			p {
				font-size: 13px;
				line-height: 22px;
				text-indent: 2em;
				text-align: justify;
				color: $six-color;
			}
			a {
				color: $main-color;
			}
		}
	}
	&_right {
		position: relative;
		@include size(500px,324px);
	}
	&_list {
		li {
			float: left;
			position: relative;
			@include size(160px,82px);
			margin-top: 19px;
			margin-right: 10px;
			text-align: center;
			background-color: #e6f3ff;
			transition: all .3s;
			&:nth-child(3n+3) {
				margin-right: 0;
			}
			&:hover {
				box-shadow: 0 2px 10px #89b3ec;
			}
			a {
				display: block;
			}
			i {
				font-size: 40px;
				display: block;
				margin: 6px 0;
				color: #0362bb;
			}
			p {
				position: absolute;
				bottom: 0;
				left: 0;
				width: 100%;				
				@include lineht(32px);
				font-weight: bold;
				@include colbg($white-color,#89b3ec);				
			}
		}
	}
	&_pic {
		position: absolute;
		bottom: 0;
		right: 0;
		overflow: hidden;
		@include size(330px,82px);
		@extend .bg_cover;
		transition: all .3s;
		&:hover {
			box-shadow: 0 2px 10px #89b3ec;
		}
	}
	&_btm {
		overflow: hidden;
		width: 100%;
		height: 90px;
		@extend .clearfix;
		li {
			float: left;
			@include size(285px,90px);
			line-height: 90px;
			margin-right: 20px;
			text-align: center;
			font-size: 18px;
			font-weight: bold;
			color: $white-color;
			&:last-child {
				margin-right: 0;
			}
			a {
				display: block;
			}
			&:nth-child(1) {
				background-color: #d45757;
			}
			&:nth-child(2) {
				background-color: #f0a920;
			}
			&:nth-child(3) {
				background-color: #68c531;
			}
			&:nth-child(4) {
				background-color: #5ea8dc;
			}
			background: url(../img/huicui_bg.png) no-repeat left center;
			background-size: contain;
		}
	}
}