.interact {
	&_news {
		&_title {
			@include lineht(36px);
			h5 {
				font-size: 18px;
				font-weight: bold;
				i {
					margin-right: 12px;
				}
				&.red {
					color: $deputy-color;
				}
				&.blue {
					color: $main-color;
				}
			}
		}
	}
	&_left {
		width: 480px;
		&_banner {
			width: 100%;
			height: 176px;
			margin-bottom: 4px;
			@extend .bg_cover;
		}
	}
	&_right {
		width: 640px;
	}
	&_btm {
		@extend .clearfix;
		li {
			float: left;
			margin-right: 12px;
			@include size(392px,90px);
			line-height: 90px;
			font-size: 26px;
			font-weight: bold;
			color: $main-color;
			text-align: center;
			box-shadow: 0 2px 10px rgba($color: #000000, $alpha: 0.6);
			@include bg('../img/interact_bg.png',cover);
			a {
				display: block;
			}
			&:last-child {
				margin-right: 0;
			}
		}
	}
}