.listing {
	padding: 0 30px;
	margin-bottom: 70px;
	background-color: $white-color;
	&_crumb {
		@extend .text_ellipsis;
		position: relative;
		padding-left: 12px;
		@include lineht(60px);
		color: #222;
		&:before {
			content: '';
			display: block;
			@include position(0,50%);
			transform: translateY(-50%);
			@include size(3px,16px);
			background-color: $main-color;
		}
		a {
			margin: 0 4px;
			&:hover {
				color: $main-color;
			}
		}
	}
	&_left {
		width: 870px;
		padding: 0 30px 20px 18px;
		border-top: 1px solid $border-color;
		&_title {
			@include lineht(70px);
			font-weight: bold;
			font-size: 18px;
			color: $main-color;
			cursor: pointer;
			i {
				margin-right: 8px;
				font-size: 20px;
				color: #89b3ec;
			}
		}
		&_unit {
			position: relative;
			min-height: 500px;
			padding-bottom: 80px;
		}
	}
	&_right {
		width: 270px;
		overflow: hidden;		
		&_box {
			margin-bottom: 30px;
			border: 1px solid $border-color;
		}
		&_title {
			overflow: hidden;
			@include lineht(70px);
			font-size: 22px;
			font-weight: bold;
			text-align: center;
			color: $white-color;
			cursor: pointer;
			@include bg('../img/news_title_bg.jpg',cover);
		}
		&_menu {			
			ul li {
				position: relative;
				@include lineht(56px);
				text-align: center;
				font-size: 18px;
				border-bottom: 1px solid $border-color;
				&:last-child {
					border-bottom: none;
				}
				a {
					display: block;
				}
				&.active,
				&:hover {
					color: $main-color;
				}
				i {
					@include position(10px,50%);
					font-size: 20px;
					transform: translateY(-50%) rotate(90deg);
					color: $main-color;
				}
			}
		}
	}
	&_page {
		position: absolute;
		bottom: 20px;
		left: 0;
		width: 100%;
	}
}