.banner {
	&_box {
		position: relative;
		overflow: hidden;
		@include size(410px, 324px);

		.hd ul {
			position: absolute;
			bottom: 20px;
			right: 12px;
			z-index: 3;
			@extend .clearfix;

			li {
				float: left;
				@include size(12px, 12px);
				margin: 0 5px;
				background-color: $white-color;
				border-radius: 50%;
				cursor: pointer;
				&.on {
					background-color: $main-color;
				}
			}
		}

		.bd ul {
			@include position(0, 0);

			li {
				position: relative;
				@include size(410px, 324px);
				@extend .bg_cover;
				a {
					display: block;
				}
				.banner_txt {
					position: absolute;
					bottom: 0;
					left: 0;
					display: block;
					height: 54px;
					line-height: 54px;
					padding-left: 14px;
					font-size: 14px;
					width: 100%;
					color: $white-color;
					background-color: rgba(0, 0, 0, .7);

					span {
						display: block;
						width: 68%;
						@extend .text_ellipsis;
					}
				}
			}
		}
	}
}
